<template>
  <section>
    <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Billing" :listType="['List']"
      :RenderRowActionMethod="() => { }" SubModuleCode="Billing" IdentityColumn="service_id"
      :HeaderText="$t('Services')" :ListData="ServiceListing" :HeaderData="Headers" :callbackfunction="FetchData"
      :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
      :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy"
      :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
      v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
      v-on:SortdataButtonClick="sortdata">
      <template v-slot:slotdata="{ data }">
        <template v-if="data.column.COLUMN_NAME == 'status_id'">
          <listing-status :isfor="'SERVICE'" ref="updatestatus" v-bind:dataid="data.row.service_id" data-controller="Crm"
            v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id" data-action="index"
            v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'" v-bind:callbackfunction="FetchData"
            v-bind:isselectedvaluestring="true" v-bind:title="data.row.status_id == 1001 ? 'Active' : 'Inactive'">
          </listing-status>
          <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
            <span v-bind:class="getStatusClass(data.row.status_id, 'STATUS_ID')">
              {{ data.row[data.column.COLUMN_NAME] }}
            </span>
          </a>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'service_name'">
          <a href="javascript:;" v-on:click="openServiceDetail(data.row.service_id)"><i aria-hidden="true"></i>{{
            data.row.service_name }}
          </a>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'created_at'">
          <span>
            {{ $d(data.row.created_at) }}
          </span>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'is_taxable'">
          <span v-if="data.row.is_taxable">
            {{ $t('Yes') }}
          </span>
          <span v-else>{{ $t('No') }}</span>
        </template>

        <template v-if="data.column.COLUMN_NAME == 'client_count'">
          <span>
            <a id="ancProjectDetail"
              :class="{ 'text-dark text-center': true, 'disabled': (parseInt(data.row.client_count) == 0) }"
              href="javascript:;" v-on:click="OpenClientListPopup(data.row.service_id)">
              <i class="fa fa-user" aria-hidden="true"></i>
              <span class="noti-circle noti-blue">{{ data.row.client_count }}</span>
            </a>
          </span>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'plan_count'">
          <span>
            <a id="ancProjectDetail" :class="{ 'text-dark text-center': true, 'disabled': (data.row.plan_count == 0) }"
              href="javascript:;" v-on:click="OpenPLanListPopup(data.row.service_id)">
              <i class="fa fa-file-o" aria-hidden="true"></i>
              <span class="noti-circle noti-orange">{{ data.row.plan_count }}</span>
            </a>
          </span>
        </template>
      </template>
    </tg-list>
    <AddNewService v-if="isCreateService" :callbackfunction="FetchData" :planCode="BillingTypeOptions"
      :isForService="isForService" :serviceId="serviceId" :billingTypeOptions="BillingTypeOptions"
      :reqform="'fromServiceListing'">
    </AddNewService>
    <ClientListingPopup v-if="isOpenClientListPopup" planId="0" :serviceId="serviceId" reqFrom="SERVICE"
      @close="isOpenClientListPopup = false; serviceId = 0;"></ClientListingPopup>
    <planPopupForService v-if="isOpenPLanListPopup" :serviceId="serviceId"
      @close="isOpenPLanListPopup = false; serviceId = 0;" reqFrom="SERVICE"></planPopupForService>
  </section>
</template>
<script>
import DataService from "../../services/DataService";
import Schema from "../PlanService/ServiceSchema";
import Vue from "vue";
import AddNewService from "../../components/Modals/AddNewService.vue";
import ClientListingPopup from '../../components/Modals/ClientListingPopup.vue';
import planPopupForService from '../../components/Modals/planPopupForService.vue';
export default {
  components: { AddNewService, ClientListingPopup, planPopupForService },
  data() {
    return {
      Headers: [
        {
          COLUMN_NAME: "service_name",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('ServiceName'),
          DISPLAY_ORDER: 1,
        },
        {
          COLUMN_NAME: "service_code",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('ServiceType'),
          DISPLAY_ORDER: 2,
        },
        {
          COLUMN_NAME: "usage_measurement",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('UsageMeasurement'),
          DISPLAY_ORDER: 3,
        },
        {
          COLUMN_NAME: "description",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('CADescription'),
          DISPLAY_ORDER: 4,
        },
        {
          COLUMN_NAME: "is_taxable",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('IsTaxable'),
          DISPLAY_ORDER: 5,
        },
        {
          COLUMN_NAME: "created_at",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('CreatedDate'),
          DISPLAY_ORDER: 6,
        },
        {
          COLUMN_NAME: "client_count",
          DATA_TYPE: "text",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('clientmapped'),
          DISPLAY_ORDER: 7
        },
        {
          COLUMN_NAME: "plan_count",
          DATA_TYPE: "text",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('planmapped'),
          DISPLAY_ORDER: 8
        },
        {
          COLUMN_NAME: "status_id",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: this.$t('Status'),
          DISPLAY_ORDER: 9,
        },
      ],
      StatusListForListing: [
        {
          STATUS: "Active",
          STATUS_ID: 1001,
          STATUS_COLOR: "#ffffff",
          STATUS_BG_COLOR: "#28a745",
        },
        {
          STATUS: "Inactive",
          STATUS_ID: 1002,
          STATUS_COLOR: "#ffffff",
          STATUS_BG_COLOR: "#d72435",
        },
      ],
      listheaderbuttons: Schema.buttons,
      leftSearchFields: Schema.leftsearchSchema,
      listActions: Schema.listActions,
      list: [],
      isForService: true,
      TotalRecords: 0,
      pageSize: 10,
      pageNum: 1,
      sortBy: "",
      searchCondition: "",
      GroupStart: 1,
      GroupEnd: 3,
      PlanId: 0,
      CurrentPage: 1,
      TotalPages: 0,
      planCode: "Billing",
      sortExp: "",
      ServiceListing: [],
      isCreateService: false,
      noRecords: false,
      serviceId: 0,
      BillingTypeOptions: [],
      isOpenClientListPopup: false,
      isOpenPLanListPopup: false
    };
  },
  created: async function () {
    debugger;
    var vm = this;
    Schema.bindleftCommonSearchdropdown(this);
    await vm.GetExtraFeatures()
    // await Schema.bindleftCommonSearchdropdown(vm);
    await vm.FetchData();
  },
  methods: {
    OpenPLanListPopup: function (id) {
      this.serviceId = id;
      this.isOpenPLanListPopup = !this.isOpenPLanListPopup;
    },
    OpenClientListPopup: function (id) {
      this.serviceId = id;
      this.isOpenClientListPopup = !this.isOpenClientListPopup;
    },
    GetExtraFeatures: async function () {
      var vueObj = this;
      var url = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
      await DataService.CheckExtraFeatures(url).then(function (response) {
        var parseData = JSON.parse(response.data);
        vueObj.BillingTypeOptions = parseData;
        if (vueObj.BillingTypeOptions[0].FEATURE_CODE == 'SUBSCRIPTION_BILLING') {
          vueObj.Headers[2].VISIBLE = false;
        }

      });
    },
    FetchData: async function () {
      var vueObj = this;
      vueObj.PlanListing = [];
      var url = `pagesize=${vueObj.pageSize}&pagenumber=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&search=${vueObj.searchCondition}`;
      await DataService.GetBillingServiceListing(url).then(function (response) {
        debugger;
        vueObj.Headers.forEach((column) => {
          column.settings = null;
          if (column.COLUMN_NAME == "status_id" || column.COLUMN_NAME == "client_count" || column.COLUMN_NAME == "plan_count" || column.COLUMN_NAME == "created_at" || column.COLUMN_NAME == "service_name" || column.COLUMN_NAME == "is_taxable") {
            column.settings = {
              isInSlot: true,
            };
          }
        });
        if (response.data != "" && response.data != null) {
          if (response.data.result.data.length > 0) {
            vueObj.ServiceListing = response.data.result.data;
            vueObj.ServiceListing.forEach((plan) => {
              if (plan.is_taxable == null) {
                plan.is_taxable = false;
              }
              ['created_at'].forEach((key) => {
                if (plan[key]) {
                  plan[key] = new Date(plan[key]);
                }
              });
              Vue.set(plan, "isSelected", false); // Use Vue.set to ensure reactivity
              if (plan.plan_count > 0) {
                Vue.set(plan, "isCheckBoxDisabled", true);
              }
              else {
                Vue.set(plan, "isCheckBoxDisabled", false);
              }
              // Vue.set(plan, "isCheckBoxDisabled", false);
            });
            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
            vueObj.TotalPages = Math.ceil(
              vueObj.TotalRecords / vueObj.pageSize
            );
            vueObj.CurrentPage = vueObj.pageNum;
            vueObj.noRecord = false;
          } else {
            vueObj.noRecord = true;
            vueObj.TotalRecords = 0;
            vueObj.ServiceListing = [];
          }
        } else {
          vueObj.noRecord = true;
          vueObj.TotalRecords = 0;
          vueObj.ServiceListing = [];
        }
      });
    },
    leftCommonSearch(value) {
      console.log("value", value);
      this.pageNum = value.PageNumber;
      this.isPaged = 1;
      if (typeof value.searchCondition != "undefined") {
        this.searchCondition = value.searchCondition;
      } else {
        this.searchCondition = "";
      }
      this.FetchData();
    },
    pagerMethod(value) {
      this.pageSize = value.PageSize;
      this.pageNum = value.PageNumber;
      this.FetchData();
    },
    openServiceDetail: function (id) {
      var vm = this;
      vm.serviceId = id;
      vm.isCreateService = true;

    },
    commonHeaderButtonClick(event) {
      switch (event.callbackfunction) {
        case "Add":
          this.AddNew();
          break;
        case "Delete":
          this.DeleteService();
          break;
      }
    },
    AddNew: function () {
      var vm = this;
      vm.serviceId = 0;
      vm.isCreateService = true;
    },
    getStatusClass: function (status) {
      var returnClass = "";
      switch (status) {
        case "Active":
          returnClass = "px-2 text-center bg-success text-white d-block";
          break;
        case "Inactive":
          returnClass = "px-2 text-center bg-danger text-white d-block";
          break;
        default:
          returnClass = "";
      }
      return returnClass;
    },
    DeleteService: function (event) {
      var vm = this;
      var val = "";
      $(".chkItems:checkbox:checked")
        .not("[id^='chkAll']")
        .each(function () {
          if (val.length > 0) val += ",";
          val += $(this).val();
        });
      if (val.length > 0) {
        vm.confirmR(
          vm.$t("ConfirmDelete"),
          true,
          vm.$t("Delete") + "  " + vm.$t("Plan"),
          function () {
            var postJSON = {
              ids: val,
              moduleName: "BILLING",
              refCode: "TALYGEN_TICKET_SERVICE",
            };
            var objectPostString = JSON.stringify(postJSON);
            DataService.CommonCommonDelete(objectPostString).then(
              (response) => {
                if (response.status == 200) {
                  vm.FetchData();
                  vm.ShowAlert(
                    vm.$t("DeletedSuccess", [vm.$t("Service")]),
                    "Success",
                    true,
                    vm.$t("Alert")
                  );
                } else {
                  vm.ShowAlert(
                    vm.$t("DeletedError", [vm.$t("Service")]),
                    "failure",
                    true,
                    vm.$t("Alert")
                  );
                }
              }
            );
          }
        );
      } else {
        vm.ShowAlert(
          vm.$t("Selectanyrecordtodelete", [vm.$t("Service")]),
          "failure",
          true,
          vm.$t("Alert")
        );
      }
    },
    sortdata: function (sortBy) {
      this.sortBy = sortBy;
      var sortExp = "ASC";
      if (this.sortExp == "ASC") {
        sortExp = "DESC";
      }
      this.sortExp = sortExp;
      this.sortBy = sortBy;
      this.sortExp = sortExp;
      this.pageNum = 1;
      this.GroupStart = this.pageNum;
      this.GroupEnd = this.pageNum + 2;
      this.FetchData();
    },
  },
};
</script>
