<template>
  <section>
    <div class="my-popups">
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
          <div class="modal-content">
            <div class="modal-header ui-draggable-handle py-1">
              <h5 name="header" class="pt-2">{{ $t("PlanMappedWith") }}</h5>
              <button class="close" v-on:click="ClosePopUp">
                <em aria-hidden="true" class="fa fa-times"></em>
              </button>
            </div>
            <div class="modal-body">
              <div class="listing">
                <div class="table-responsive table-fix-header">
                  <table
                    class="table table-bordered mt-0"
                    :class="{
                      'subscription-table': CodePlan === 'SUBSCRIPTION_BILLING',
                      'utility-table': CodePlan === 'UTILITY_BILLING',
                    }"
                  >
                    <thead class="thead-bg">
                      <tr>
                        <th>{{ $t("ClientName") }}</th>
                        <th>{{ $t("PlanName") }}</th>

                        <th v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{ $t("Frequency") }}
                        </th>
                        <th v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{ $t("Units") }}
                        </th>
                        <th v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{ $t("Price(per Unit)") }}
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="item in clientList" :key="item.CLIENT_NAME">
                        <td>{{ item.CLIENT_NAME }}</td>
                        <td>{{ item.PLAN_NAME }}</td>

                        <td v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{ item.FREQUENCY }}
                        </td>
                        <td v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{ $n(item.UNITS) }}
                        </td>
                        <td v-if="CodePlan === 'SUBSCRIPTION_BILLING'">
                          {{
                            $n(
                              item.PRICE,
                              "currency",
                              $getLocaleByCurrency(CurrencyCode)
                            )
                          }}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
  props: {
    planId: {
      type: String,
      required: false,
    },
    serviceId: {
      type: String,
      required: false,
    },
    reqFrom: {
      type: String,
      required: false,
    },
    CodePlan: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      clientList: [],
      CurrencyCode: "PKR",
    };
  },

  created() {
    var vueObj = this;
    vueObj.CurrencyCode = vueObj.GetUserInfo.Currency.replace(/[()]/g, "");
    var url = `planId=${vueObj.planId}&serviceId=${vueObj.serviceId}&reqFrom=${vueObj.reqFrom}`;
    DataService.GetClientListMappedWithPlan(url).then(function (response) {
      if (response.data != null) {
        vueObj.clientList = response.data.result;
      }
    });
  },

  watch: {
    CodePlan(newVal, oldVal) {
      debugger;
      console.log("CodePlan changed from", oldVal, "to", newVal);
    },
  },

  methods: {
    ClosePopUp() {
      this.$emit("close");
    },
  },
};
</script>
<style scoped>
/* Default Table Reset */
.table th,
.table td {
  text-align: left;
  vertical-align: middle;
  white-space: nowrap;
}

/* Case 1: UTILITY_BILLING (2 columns) */
.utility-table th:nth-child(1),
.utility-table td:nth-child(1) {
  width: 60%;
}

.utility-table th:nth-child(2),
.utility-table td:nth-child(2) {
  width: 80%;
}

/* Case 2: SUBSCRIPTION_BILLING (5 columns) */
.subscription-table th:nth-child(1),
.subscription-table td:nth-child(1) {
  width: 25%;
}

.subscription-table th:nth-child(2),
.subscription-table td:nth-child(2) {
  width: 25%;
}

.subscription-table th:nth-child(3),
.subscription-table td:nth-child(3),
.subscription-table th:nth-child(4),
.subscription-table td:nth-child(4),
.subscription-table th:nth-child(5),
.subscription-table td:nth-child(5) {
  width: 16%;
  text-align: center;
}
</style>
