<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2" v-if="serviceId == 0">
                                {{ $t('CreateService') }}
                            </h5>
                            <h5 name="header" class="pt-2" v-else-if="serviceId != 0">
                                {{ $t('EditService') }}
                            </h5>
                            <button class="close" v-on:click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validateService" :buttons="buttons" :schema="FormSchema">
                                <template v-slot:tgslot-ServiceType="{ data }">
                                    <div class="form-group mb-0">
                                        <select v-model="data.fieldInfo.value" class="form-control">
                                            <option v-for="item in data.fieldInfo.config.OptionsForBind" :key="item.value" :value="item.value">{{ item.name }}</option>
                                        </select>
                                        <span class="text-warning" style="font-size: 10px; font-weight: 400;">{{ getServiceTypeDesc(data.fieldInfo.config.OptionsForBind, data.fieldInfo.value) }}</span>
                                    </div>
                                </template>
                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>
<script>
import DataServices from "../../services/DataService";
export default {
    props: {
        planCode: {
            type: String,
            required: false
        },
        isForService: {
            type: Boolean,
            required: false
        },
        callbackfunction: {
            type: Function,
            required: false
        },
        serviceId: {
            type: Number,
            required: false
        },
        billingTypeOptions: {
            type: Array,
            default: []
        },
        paymentType: {
            type: String,
            required: false
        },
        reqform: {
            type: String,
            required: false
        }
    },
    data() {
        return {
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('billingtype'),
                            name: "BillingTypeOptions",
                            value: "",
                            visibility: true,
                            config: {
                                options: [],
                                onChange: this.onChangeOfBillingType,
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        // {
                        //     astype: "RadioField",
                        //     label: this.$t('SubscriptionType'),
                        //     name: "service_for",
                        //     visibility: true,
                        //     value: "BILLING",
                        //     config: {
                        //         options: [{ value: "BILLING", name: "Billing" }, { value: "UTILITY", name: "Utility" }],
                        //         onChange: this.onChangeOfBillingType,
                        //     },
                        //     validationRules: { "required": true},
                        // },
                        {
                            astype: "SelectField",
                            label: this.$t('UtilityType'),
                            name: 'meterTypeId',
                            visibility: false,
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onGetUnitByUtilityTypeId
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('ServiceType'),
                            name: 'ServiceType',
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('name'),
                            name: "Name",
                            value: "",
                            placeholder: "Enter Name",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('UsageMeasurement'),
                            name: "UsageMeasurement",
                            visibility: false,
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [

                        {
                            astype: "SelectField",
                            label: this.$t('WorkType'),
                            name: 'WorkType',
                            value: "",
                            visibility: false,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ClientDefinedIdentifier'),
                            name: "ClientDefinedIdentifier",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('BillingFrequency'),
                            name: "BillingFrequency",
                            visibility: false,
                            disabled: false,
                            value: "",
                            config: {
                                options: [{ value: "1", name: "One Time" }, { value: "2", name: "Recurring" }]
                            }

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: this.$t("Active") }, { value: "1002", name: this.$t("Inactive") }]
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('istaxble'),
                            name: "IsTaxble",
                            value: "",
                            disabled: false,
                            config: {
                                onChange: this.ChangeIsTaxble
                            },
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('SelectTaxType'),
                            name: "SelectedTax",
                            value: "",
                            mode: "tag",
                            placeholder: this.$t('SelectTaxType'),
                            visibility: false,
                            config: {
                                options: []
                            },
                            validationRules: {
                                "required": false
                            },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 4
                            },
                            validationRules: { "required": false, max: 2000 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.ClosePopUp
                }
            ],
            serviceDetail: []
        }
    },
    // mounted: function () {
    //     var vm = this;
    //     if (vm.planCode != '') {
    //         vm.ChangeServiceFor(vm.planCode);
    //     }
    // },
    created: function () {
        var vm = this;
        let billingTypeOptions = []
        if (vm.billingTypeOptions.length > 1) {
            vm.billingTypeOptions.forEach(element => {
                billingTypeOptions.push({ value: element.FEATURE_CODE, name: element.FEATURE_NAME });
            });

            vm.FormSchema.forEach(function (element, index) {
                element.Data.forEach(function (field, findex) {
                    if (field.name == "BillingTypeOptions") {
                        field.config.options = billingTypeOptions;
                        field.visibility = true;
                        element.layoutType = "triple"
                    }
                    if (field.name == "BillingFrequency") {
                        field.visibility = false;
                    }
                });
            });

        } else {
            vm.FormSchema.forEach(function (element, index) {
                element.Data.forEach(function (field, findex) {
                    if (field.name == "BillingTypeOptions") {
                        field.config.options.push({ name: vm.billingTypeOptions[0].FEATURE_NAME, value: vm.billingTypeOptions[0].FEATURE_CODE });
                        field.value = vm.billingTypeOptions[0].FEATURE_CODE;
                        element.layoutType = "double";
                        if (field.value == "UTILITY_BILLING") {
                            vm.FormSchema[0].Data[1].visibility = true;
                        }
                    }
                    else if (field.name == "CustomerType" && vm.billingTypeOptions[0].FEATURE_CODE == "UTILITY_BILLING") {
                        field.visibility = true;
                        element.layoutType = "triple";
                    }
                    if (field.name == "BillingFrequency") {
                        field.visibility = false;
                    }
                });
            });
        }

        var checkIfUtilityBillingExists = this.billingTypeOptions.filter(function (x) { x.FEATURE_CODE == "UTILITY_BILLING" });
        if (checkIfUtilityBillingExists.length > 0) {
            vm.FormSchema.forEach(function (element, index) {
                element.Data.forEach(function (field, findex) {
                    if (field.name == "ClientDefinedIdentifier") {
                        field.visibility = true;
                    }
                });
            });
        }

        vm.GetTaxCategory();
        vm.GetDDLServiceType();
        vm.GetDDLWorkType();
        setTimeout(() => { vm.ChangeServiceFor(); this.bindBillingFrequency(vm.paymentType); }, 500);
        if (vm.serviceId > 0) {
            setTimeout(function () {
                vm.BindEditDetail(vm.serviceId);
            }, 1000);
        }
        vm.getMeterTypes();
    },
    methods: {
        getServiceTypeDesc: function(items, selectedvalue) {
            let ths = this;
            let returnValue = "";
            items.forEach(element => {
                if(element.value == selectedvalue){
                    returnValue = ths.$t(element.second_value);
                }
            });
            return returnValue;
        },
        onGetUnitByUtilityTypeId: function (x, y, z) {
            var vm = this;
            if (y.value == "") {
                vm.$refs.validateService.updateProperty("UsageMeasurement", "visibility", false);
                vm.$refs.validateService.UpdateKeyValue();
            }
            else {
                var params = "id=&moduleName=&type=UTILITY_UNIT_OF_MEASUREMENTS&search=&refId=" + y.value;
                DataServices.GetDDLData(params).then((response) => {
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            vm.$refs.validateService.updateProperty("UsageMeasurement", "visibility", true);
                            vm.$refs.validateService.updateProperty("UsageMeasurement", "config.options", response.data.DATA);
                            vm.$refs.validateService.UpdateKeyValue();
                        }
                    }
                })
            }
        },
        async getMeterTypes() {
            const vm = this;
            try {
                const response = await DataServices.GetMeterTypes();
                if (response?.data) {
                    const responseData = JSON.parse(response.data.result).data;

                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {

                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.meter_id,
                                name: item.meter_name
                            });

                        });
                        vm.$refs.validateService.UpdateKeyValue();

                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Meter types:', error);
            } finally {
                //
            }
        },
        onChangeOfBillingType: function (x, y) {
            let vm = this;
            if (y.value == "UTILITY_BILLING") {
                vm.FormSchema[0].Data[1].visibility = true;
            }
            else {
                vm.FormSchema[0].Data[1].visibility = false;
            }
        },
        bindBillingFrequency: function (paymentType) {
            var vm = this;
            if (paymentType == 'One Time') {
                vm.$refs.validateService.updateProperty("BillingFrequency", "value", "1");
                vm.$refs.validateService.UpdateKeyValue();
            }
            else {
                vm.$refs.validateService.updateProperty("BillingFrequency", "value", "2");
                vm.$refs.validateService.UpdateKeyValue();
            }

        },
        BindEditDetail: function (serviceId) {
            var vm = this;
            var url = `serviceId=${serviceId}`;
            DataServices.GetServiceListById(url).then(response => {
                if (response.data.status == 'Success') {
                    if (JSON.parse(response.data.result).data.length > 0) {
                        vm.serviceDetail = JSON.parse(response.data.result).data;
                        vm.FormSchema[0].Data[0].value = (vm.serviceDetail[0].SERVICE_FOR).toUpperCase();
                        if (vm.serviceDetail[0].SERVICE_FOR == 'UTILITY_BILLING') {
                            vm.FormSchema[0].Data[1].visibility = true;
                            vm.FormSchema[0].Data[1].value = vm.serviceDetail[0].METER_TYPE_ID;
                        }
                        else {
                            vm.FormSchema[0].Data[1].visibility = false;
                        }
                        if (vm.serviceDetail[0].METER_TYPE_ID != null) {
                            vm.BindUnitByUtilityId(vm.serviceDetail[0].METER_TYPE_ID, vm.serviceDetail[0].USAGE_MEASUREMENT_ID);
                        }
                        debugger
                        if (vm.serviceDetail.WORK_TYPE_ID != null) {
                            vm.FormSchema[0].Data[5].value = vm.serviceDetail[0].WORK_TYPE_ID;
                        }
                        vm.FormSchema[0].Data[2].value = vm.serviceDetail[0].SERVICE_CODE_ID;
                        vm.FormSchema[0].Data[3].value = vm.serviceDetail[0].SERVICE_NAME;
                        vm.FormSchema[0].Data[4].value = vm.serviceDetail[0].USAGE_MEASUREMENT_ID;

                        vm.FormSchema[1].Data[1].value = vm.serviceDetail[0].CLIENT_DEFINED_IDENTIFIER;
                        if (vm.serviceDetail[0].SERVICE_TYPE_CODE == 'Recurring' || vm.serviceDetail[0].SERVICE_TYPE_CODE == 'Minimum Fee' || vm.serviceDetail[0].SERVICE_TYPE_CODE == 'Recurring Arrears') {
                            vm.FormSchema[1].Data[2].value = '2'
                        }
                        else {
                            vm.FormSchema[1].Data[2].value = '1'
                        }
                        vm.FormSchema[1].Data[3].value = vm.serviceDetail[0].STATUS_ID;
                        vm.FormSchema[2].Data[0].value = vm.serviceDetail[0].DESCRIPTION;
                        if (vm.serviceDetail[0].is_taxable) {
                            vm.FormSchema[1].Data[4].value = vm.serviceDetail[0].is_taxable;
                            var TaxIds = vm.serviceDetail[0].TAX_IDS.split(',');
                            var filteredData = vm.TaxData.filter(item => TaxIds.includes(item.value));
                            var TaxArray = [];
                            filteredData.forEach(itm => {
                                var Obj = {
                                    value: itm.value,
                                    label: itm.name
                                }
                                TaxArray.push(Obj);
                            })
                            vm.FormSchema[1].Data[5].value = TaxArray;
                            vm.FormSchema[1].Data[5].visibility = true;

                        } else {
                            vm.FormSchema[1].Data[5].visibility = false;
                        }
                    }
                }
            })
        },
        BindUnitByUtilityId: function (utilityId, unitId) {
            var vm = this;
            var params = "id=&moduleName=&type=UTILITY_UNIT_OF_MEASUREMENTS&search=&refId=" + utilityId;
            DataServices.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateService.updateProperty("UsageMeasurement", "visibility", true);
                        vm.$refs.validateService.updateProperty("UsageMeasurement", "config.options", response.data.DATA);
                        vm.$refs.validateService.updateProperty("UsageMeasurement", "value", unitId);
                        vm.$refs.validateService.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLServiceType: function () {
            var vm = this;
            let refId = []
            vm.billingTypeOptions.forEach(function (x, i) {
                refId.push(x.FEATURE_ID);
            })
            var params = "id=&moduleName=&type=TICKETING_SERVICE_TYPE&search=&refId=" + (refId.join(','));
            DataServices.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = '';
                    json = response.data;
                    if (vm.paymentType == 'One Time' && json != null) {
                        json = json.DATA.filter(item => item.name !== "Order Based" && item.name !== "Recurring" && item.name !== "Recurring Arrears" && item.name !== "Usage Based");
                        vm.$refs.validateService.updateProperty("ServiceType", "config.options", json);
                        vm.$refs.validateService.UpdateKeyValue();
                    }
                    else if (vm.paymentType != 'One Time' && json != null) {
                        vm.$refs.validateService.updateProperty("ServiceType", "config.options", json.DATA);
                        vm.$refs.validateService.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLWorkType: function () {
            debugger
            var vm = this;
            var url = `id=&moduleName=TICKETING&type=FSM_WORKTYPE&search=&refId=`;
            DataServices.GetDDLData(url).then(response => {
                debugger
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.FormSchema[1].Data[5].config.options.push(json);

                        // vm.$refs.validateService.updateProperty("WorkType", "config.options", json);
                        // vm.$refs.validateService.UpdateKeyValue();
                    }
                }
            })
        },
        ChangeServiceFor: function () {
            var vm = this;
            if (vm.billingTypeOptions[0].FEATURE_CODE == 'UTILITY_BILLING') {
                // vm.$refs.validateService.updateProperty("UsageMeasurement", "visibility", true);
                vm.$refs.validateService.updateProperty("WorkType", "visibility", false);
                vm.$refs.validateService.UpdateKeyValue();
            }
            else if (vm.billingTypeOptions[0].FEATURE_CODE == "SUBSCRIPTION_BILLING") {
                vm.$refs.validateService.updateProperty("WorkType", "visibility", false);
                // vm.$refs.validateService.updateProperty("UsageMeasurement", "visibility", false);
                vm.$refs.validateService.UpdateKeyValue();
            }
        },
        ClosePopUp: function () {
            var vm = this;
            vm.$parent.isCreateService = false;
        },
        async SaveData() {
            var vm = this;
            const result = await vm.$refs.validateService.validateForm.validate();
            if (result) {
                var serviceFor = vm.$refs.validateService.getPropertyValue("BillingTypeOptions", "value");
                var meterTypeId = vm.$refs.validateService.getPropertyValue("meterTypeId", "value");
                var serviceType = vm.$refs.validateService.getPropertyValue("ServiceType", "value");
                var name = vm.$refs.validateService.getPropertyValue("Name", "value");
                var usageMeasurement = vm.$refs.validateService.getPropertyValue("UsageMeasurement", "value");
                var workType = vm.$refs.validateService.getPropertyValue("WorkType", "value");
                var clientDefinedIdentifier = vm.$refs.validateService.getPropertyValue("ClientDefinedIdentifier", "value");
                var status = vm.$refs.validateService.getPropertyValue("Status", "value");
                var description = vm.$refs.validateService.getPropertyValue("Description", "value");
                var istaxable = vm.$refs.validateService.getPropertyValue("IsTaxble", "value");
                var SelectedTax = vm.$refs.validateService.getPropertyValue("SelectedTax", "value");
                if (SelectedTax != '') {
                    var TaxVal = SelectedTax.map(item => item.value).join(',');
                }
                var objData =
                {
                    ID: vm.serviceId,
                    ServiceFor: serviceFor,
                    MeterTypeId: meterTypeId,
                    ServiceType: serviceType,
                    Name: name,
                    UsageMeasurement: usageMeasurement,
                    WorkType: workType,
                    ClientDefinedIdentifier: clientDefinedIdentifier,
                    Status: status,
                    Description: description,
                    Istaxable: istaxable,
                    SelectedTax: TaxVal
                }
                var obj = JSON.stringify(objData);
                var params = `objData=${encodeURIComponent(obj)}`;
                await DataServices.CreateNewService(params).then(response => {
                    if (response.data != null) {
                        if (response.data.Status == "Success" && response.data.Code == "1") {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Service')]), "Success", true, vm.$t('Alert'));
                            vm.ClosePopUp();
                            if (vm.isForService == true) {
                                vm.callbackfunction();
                            }
                            vm.isLoading = false;
                        }
                        else if (response.data.Status == "Success" && response.data.Code == "2") {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Service')]), "Success", true, vm.$t('Alert'));
                            vm.ClosePopUp();
                            if (vm.isForService == true) {
                                vm.callbackfunction();
                            }
                            vm.isLoading = false;
                        }
                        else if (response.data.Status == "Success" && response.data.Code == "4") {
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Service')]), "failure", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                    }
                });


            }
        },
        ChangeIsTaxble(data, event) {
            var ths = this;
            if (data) {
                ths.FormSchema[1].Data[5].visibility = true;
            } else {
                ths.FormSchema[1].Data[5].visibility = false;
                ths.FormSchema[1].Data[5].value = '';
            }
        },
        GetTaxCategory() {
            var vm = this;
            var url = `id=&moduleName=&type=TAX_CATEGORY&search=&refId=`;
            vm.FormSchema[1].Data[5].config.options = [];
            DataServices.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.TaxData = response.data.DATA;
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[1].Data[5].config.options.push(obj);
                            })
                            vm.$refs.validateService.UpdateKeyValue();
                        }
                    }
                }
            })
        },

    }
}

</script>