<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2"> {{ $t('Plans') }} </h5>
                            <button class="close" v-on:click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">    
                            <table class="table table-bordered mt-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th>{{ $t('PlanName') }}</th>
                                        <th>{{ $t('Rate Applicable on plan') }}</th>
                                        <th>{{ $t('EffectiveDate') }}</th>
                                        <th>{{ $t('Expiration Date') }}</th>
                                        <th>{{ $t('TemplateName') }}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in planList">
                                        <td>{{ item.PLAN_NAME }}</td>
                                        <td>{{ item.IS_RATE_APPLICABLE_ON_PLAN }}</td>
                                        <td>{{ $options.filters.formatDate(item.EFFECTIVE_DATE) }}</td>
                                        <td>{{ $options.filters.formatDate(item.EXPIRATION_DATE) }}</td>
                                        <td>{{ item.TEMPLATE_NAME }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
    import DataService from '../../services/DataService';
    export default {
        props: {
            reqFrom: {
                type: String,
                required: false
            },
            serviceId: {
                type: String,
                required: false
            },
        },
        data() {
            return {
                planList: [],
            }
        },
        created: function() {
            var vueObj = this;
            var url = `serviceId=${vueObj.serviceId}&reqFrom=${vueObj.reqFrom}`;
            DataService.GetPlanListMappedWithService(url).then(function (response) {
                if(response.data != null){
                    vueObj.planList = response.data.result;
                }
            });
        },
        methods: {
            ClosePopUp: function() {
                this.$emit("close");
            }
        },
    }
</script>