import DataService from '../../services/DataService';

let buttons =
    [
        {
            title: 'AddService',
            iconClass: 'fa fa-plus',
            callbackfunction: 'Add',
            additionalClass: '',
            isdisabled: false,
            isvisible: true,
            checkPrivilege: false
        },
        {
            title: 'Delete',
            iconClass: 'fa fa-trash',
            callbackfunction: 'Delete',
            additionalClass: '',
            isdisabled: true,
            isvisible: true,
            checkPrivilege: false,
            isEnabledOnSelectedRow: true
        },
    ];
let leftsearchSchema = [
    {
        fieldName: "ServiceName",
        fieldType: "text",
        fieldIcon: "fa fa-user",
        paramName: "TTS.service_name",
        fieldSearchConditionName: ['TTS.service_name'],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'CreatedBy',
        fieldType: "radio",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: ['TTS.created_by'],
        paramName: "TTS.created_by",
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: "Status",
        fieldType: "radio",
        fieldIcon: "fa-calendar",
        paramName: "TTS.status_id",
        fieldSearchConditionName: ['TTS.status_id'],
        isSearch: false,
        value: "",
        listOptions: [{ name: 'Active', value: 1001 }, { name: 'Inactive', value: 1002 }]
    }
];


async function bindleftCommonSearchdropdown(instance) {
    var vm = instance;
    var params = `id=&moduleName=&type=USERS&search=`
    await DataService.GetDDLData(params).then((response) => {
        if (response.data != '') {
            if (response.data.DATA.length) {
                response.data.DATA.forEach(element => {
                    let obj = {
                        name: element.name,
                        value: element.value
                    }
                    vm.leftSearchFields[1].listOptions.push(obj);
                });
            }
        }
    });
}

export default { leftsearchSchema, buttons, bindleftCommonSearchdropdown };